.glow {
    box-shadow: 0 0 5px white;
    animation: animate 20s linear infinite;
}

@keyframes animate {
    0% {
        box-shadow: 3px 3px .4em #528CCB, -3px -3px .4em #FF8600;
    }
    25% {
        box-shadow: 3px 3px .4em #F31D2F, -3px -3px .4em #00D717;
    }
    50% {
        box-shadow: 3px 3px .4em #FF8600, -3px -3px .4em #528CCB;
    }
    75% {
        box-shadow: 3px 3px .4em #00D717, -3px -3px .4em #BF4ACC;
    }
    100% {
        box-shadow: 3px 3px .4em #BF4ACC, -3px -3px .4em #FF8600;
    }
}

.glow:after {
    filter: blur(40px);
}
